<template>
  <div class="order-current">
    <el-table :data="tableData" class="data-table">
      <el-table-column prop="create_time" :label="$t('order.table3.col1')" width="130"></el-table-column>
      <el-table-column prop="currency_area" :label="$t('order.table3.col2')" align="center"></el-table-column>
      <el-table-column :label="$t('order.table3.col3')" align="center">
        <template slot-scope="scope">
            <span v-if="scope.row.trade_type == 'buy'" class="c-green">{{ $t('myOrder.part5') }}</span>
            <span v-if="scope.row.trade_type == 'sell'" class="c-red">{{ $t('myOrder.part6') }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="trade_price" :label="$t('order.table3.col4')"></el-table-column>
      <el-table-column prop="trade_success_num" :label="$t('order.table3.col5')"></el-table-column>
      <el-table-column prop="trade_count_price" :label="$t('order.table3.col6')"></el-table-column>
      <el-table-column prop="trade_success_num" :label="$t('order.table3.col7')"></el-table-column>
    </el-table>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="pageOptions.page_num"
      :page-size="pageOptions.per_page_num"
      layout="prev, pager, next, jumper"
      :total="totalCount*pageOptions.per_page_num"
    ></el-pagination>

    <!-- <check-order 
      @change="getmyAdList"
      :data="checkData"
      :show.sync="dialogVisibleCheck">
    </check-order> -->
  </div>
</template>
<script>
import { getUnableTradeList, entrustCancelOption } from '@/api/trade'
import { getDealTradeList } from '@/api/orderManage'

// import CheckOrder from './CheckOrder'
export default {
  components: { },
  data() {
    return {
      // selectCoinID: null,
      // selectList: [],
      // options: [],
      // start_time: null,
      // end_time: null,
      // 分页选项
      dialogVisibleCheck: false,
      pageOptions: {
        per_page_num: 20,
        page_num: 1,
        status: 2
      },
      totalCount: 0,
      tableData: null,
      checkData: []
    }
  },
  created() {
    // this.getSelectList()
    this.getmyAdList()
  },
  methods: {
    // handleSelect() {
    //   this.getmyAdList()
    //   window.location.href = window.location.href.split('?')[0] + '?currency_id=' + this.selectCoinID
    // },
    handleRevoke({ orders_id, currency_trade_id, currency_id }) {
      this.$confirm(this.$t('add.cancleOrder'), this.$t('dialog.title'), {
        confirmButtonText: this.$t('dialog.sure'),
        cancelButtonText: this.$t('dialog.cancle'),
        type: 'warning'
      }).then(() => {
        const data = {
          'currency_trade_id': currency_trade_id,
          'currency_id': currency_id,
          'orders_id': orders_id
        }
        entrustCancelOption(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(this.$t('myOrder.part12'))
            return false
          }
          this.$message.success(this.$t('myOrder.part13'))
          this.getmyAdList()
        }).catch(() => {
        })
      }).catch(() => {
      })
    },
    handleCheck({ orders_id, currency_trade_id, currency_id }) {
      getDealTradeList({
        orders_id,
        currency_trade_id,
        currency_id,
        start: 1,
        legnth: 10000
      }).then(res => {
        this.dialogVisibleCheck = true
        this.checkData = res.data.order_list
      }).catch(() => {

      })
    },
    handSendAd() {
      this.$router.push({ name: 'release', query: {}})
    },
    handleSizeChange(val) {
      this.pageOptions.page = val
      this.getmyAdList()
    },
    handleCurrentChange(val) {
      this.pageOptions.page = val
      this.getmyAdList()
    },
    // getSelectList() {
    //   financeCenter().then(res => {
    //     if (res.status !== 1000) {
    //       this.$message.error(res.msg)
    //     }
    //     this.selectList = [...res.data.normal_currency]
    //     if (this.$route.query.currency_id) {
    //       this.selectCoinID = parseFloat(this.$route.query.currency_id)
    //     } else {
    //       this.selectCoinID = this.selectList[0].currency_id
    //     }
    //     this.getmyAdList()
    //   }).catch(() => {
    //   })
    // },
    // filterTag(value, row) {
    //   return row.trade_type === value
    // },
    // filterTag1(value, row) {
    //   return row.status === value
    // },
    // filterHandler(value, row, column) {
    //   const property = column['property']
    //   return row[property] === value
    // },
    getmyAdList() {
      // let data = {...this.pageOptions}
      // const data = {
      //   length: this.pageOptions.per_page_num,
      //   start: (this.pageOptions.page - 1) * 10
      // }
      // if (this.selectCoinID) {
      //   data.currency_id = this.selectCoinID
      // }
      // if (this.start_time) {
      //   data.start_time = Date.parse(this.start_time) / 1000
      // }
      // if (this.end_time) {
      //   data.end_time = Date.parse(this.end_time) / 1000
      // }
      getUnableTradeList(this.pageOptions)
        .then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          }
          this.tableData = [...res.data.order_list]
          this.totalCount = res.data.total_num
        })
        .catch(() => {})
    }
  }
}
</script>
<style lang="less" scoped>
.order-current {
  min-height: 770px;
  // .swicth-btn a.normal {
  //   color: #7d58d1;
  // }
  // .swicth-btn a {
  //   margin-right: 0 !important;
  // }

  // .swicth-btn a {
  //   color: #7d58d1;
  //   display: inline-block;
  //   padding: 3px 12px;
  //   margin-right: 18px;
  //   border: 1px solid #7d58d1;
  //   border-radius: 5px;
  //   font-size: 12px;
  // }
  // .title {
  //   padding-bottom: 10px;
  //   font-size: 16px;
  //   line-height: 30px;
  //   border-bottom: 1px solid #ebecf1;
  // }
  // .order-search {
  //   position: relative;
  //   padding-top: 25px;
  //   .time-input {
  //     width: 135px;
  //   }
  //   .coin {
  //     width: 150px;
  //   }
  // }
  // .data-table {
  // }
}
</style>


